</template>

<script setup>
	import {
		inject,
		nextTick,
		ref,
		getCurrentInstance
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context.$project.projectName
	const countTypeList = ref({
	})
	const getCountList=()=>{
		countTypeList.value.closeyuyueweixiuCountType = true
		countTypeList.value.hiddenyuyueweixiuCountType = true
		if(btnAuth('yuyueweixiu','首页总数')){
			getyuyueweixiuCount()
		}
		countTypeList.value.closefuwupingjiaCountType = true
		countTypeList.value.hiddenfuwupingjiaCountType = true
		if(btnAuth('fuwupingjia','首页总数')){
			getfuwupingjiaCount()
		}
		countTypeList.value.closegongzijixiaoCountType = true
		countTypeList.value.hiddengongzijixiaoCountType = true
		if(btnAuth('gongzijixiao','首页总数')){
			getgongzijixiaoCount()
		}
	}
	const yuyueweixiuCount = ref(0)
	const getyuyueweixiuCount = () => {
		context?.$http({
			url:'yuyueweixiu/count',
			method: 'get'
		}).then(res=>{
			yuyueweixiuCount.value = res.data.data
		})
	}
	const fuwupingjiaCount = ref(0)
	const getfuwupingjiaCount = () => {
		context?.$http({
			url:'fuwupingjia/count',
			method: 'get'
		}).then(res=>{
			fuwupingjiaCount.value = res.data.data
		})
	}
	const gongzijixiaoCount = ref(0)
	const getgongzijixiaoCount = () => {
		context?.$http({
			url:'gongzijixiao/count',
			method: 'get'
		}).then(res=>{
			gongzijixiaoCount.value = res.data.data
		})
	}
	const countTypeClick = (e) => {
		countTypeList.value[e] = !countTypeList.value[e]
	}
	//权限验证
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	let echarts = inject("echarts")
	const cardTypeClick = (e) =>{
		cardTypeList.value[e] = !cardTypeList.value[e]
		setTimeout(()=>{
			getCardList()
		},1000)
	}
	const cardTypeList = ref({
		closeyuyueweixiuChartType1: true,
		hiddenyuyueweixiuChartType1: true,
		closefuwupingjiaChartType1: true,
		hiddenfuwupingjiaChartType1: true,
		closefuwupingjiaChartType2: true,
		hiddenfuwupingjiaChartType2: true,
		closegongzijixiaoChartType1: true,
		hiddengongzijixiaoChartType1: true,
	})
	const getCardList = () => {
		if(btnAuth('yuyueweixiu','首页统计')){
			getyuyueweixiuChart1()
		}
		if(btnAuth('fuwupingjia','首页统计')){
			getfuwupingjiaChart1()
		}
		if(btnAuth('fuwupingjia','首页统计')){
			getfuwupingjiaChart2()
		}
		if(btnAuth('gongzijixiao','首页统计')){
			getgongzijixiaoChart1()
		}
	}
	import '@/assets/js/echarts-theme'
	const getyuyueweixiuChart1 = () => {
		nextTick(()=>{
			var sfshEchart1 = echarts.init(document.getElementById("yuyueweixiusfshEchart1"),'theme');
			context?.$http({
				url: "yuyueweixiu/group/sfsh",
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let dataList = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].sfsh);
				    yAxis.push(parseFloat((res[i].total)));
					dataList.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].sfsh
				    })
				}
				var option = {};
				option = {
    title:{
        text: '预约通过率',
        left: 'center'
    },
    legend: {
        orient: 'vertical',
        left: 'left'
    },
    tooltip: {
        trigger: 'item',
        formatter: '{b} : {c} ({d}%)'
    },
    series: [
        {
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: dataList,
            emphasis: {
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
            }
        }
    ]
}
				sfshEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				sfshEchart1.setOption(option);
				//根据窗口的大小变动图表
				sfshEchart1.resize();
			})
		})
	}
	const getfuwupingjiaChart1 = () => {
		nextTick(()=>{
			var weixiupingjiaEchart1 = echarts.init(document.getElementById("fuwupingjiaweixiupingjiaEchart1"),'theme');
			context?.$http({
				url: `fuwupingjia/value/gonghao/weixiupingjia`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let dataList = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].gonghao);
				    yAxis.push(parseFloat((res[i].total)));
					dataList.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].gonghao
				    })
				}
				var option = {};
				option = {
    title: {
        text: '服务评价',
        left: 'center'
    },
    tooltip: {
        trigger: 'item',
        formatter: '{b} : {c}'
    },
    xAxis: {
        data: xAxis,
        type: 'category',
    },
    yAxis: {
        type: 'value'
    },
    series:{
        data: yAxis,
        type: 'bar'
    }
}

				weixiupingjiaEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				weixiupingjiaEchart1.setOption(option);
				//根据窗口的大小变动图表
				weixiupingjiaEchart1.resize();
			})
		})
	}
	const getfuwupingjiaChart2 = () => {
		nextTick(()=>{
			var shifupingjiaEchart2 = echarts.init(document.getElementById("fuwupingjiashifupingjiaEchart2"),'theme');
			context?.$http({
				url: `fuwupingjia/value/gonghao/shifupingjia`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let dataList = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].gonghao);
				    yAxis.push(parseFloat((res[i].total)));
					dataList.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].gonghao
				    })
				}
				var option = {};
				option = {
    title: {
        text: '维修评价',
        left: 'center'
    },
    tooltip: {
        trigger: 'item',
        formatter: '{b} : {c}'
    },
    xAxis: {
        data: xAxis,
        type: 'category',
    },
    yAxis: {
        type: 'value'
    },
    series:{
        data: yAxis,
        type: 'bar'
    }
}

				shifupingjiaEchart2.clear()
				// 使用刚指定的配置项和数据显示图表。
				shifupingjiaEchart2.setOption(option);
				//根据窗口的大小变动图表
				shifupingjiaEchart2.resize();
			})
		})
	}
	const getgongzijixiaoChart1 = () => {
		nextTick(()=>{
			var weixiujineEchart1 = echarts.init(document.getElementById("gongzijixiaoweixiujineEchart1"),'theme');
			context?.$http({
				url: `gongzijixiao/value/gonghao/weixiujine`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let dataList = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].gonghao);
				    yAxis.push(parseFloat((res[i].total)));
					dataList.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].gonghao
				    })
				}
				var option = {};
				option = {
    title: {
        text: '工资绩效',
        left: 'center'
    },
    tooltip: {
        trigger: 'item',
        formatter: '{b} : {c}'
    },
    xAxis: {
        data: xAxis,
        type: 'category',
    },
    yAxis: {
        type: 'value'
    },
    series:{
        data: yAxis,
        type: 'bar'
    }
}

				weixiujineEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				weixiujineEchart1.setOption(option);
				//根据窗口的大小变动图表
				weixiujineEchart1.resize();
			})
		})
	}

	const init=()=>{
		getCountList()
		getCardList()
	}
	init()
</script>
